.container {
    display: flex;
    flex-direction: column;
    margin-right: 60px;
}

.link {
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    color: #8585AD;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
}

.link_active{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
    color: #F2F2F3;
}

.text{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #8585AD;
    width: 320px;
    height: 48px;
    display: flex;
    align-items: center;
    margin: 80px 0 0; 
    opacity: .4;
}

.button{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    color: #8585AD;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
    background-color: transparent;
    padding: 0;
    border: none;
}