.main {
    max-width: 1200px;
    width: 100%;
    margin: 120px auto auto;
    display: flex;
    height: 320px;
}

.container {
    display: flex;
    flex-direction: column;
    margin-right: 60px;
}

.link {
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    color: #8585AD;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
}

.button{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    color: #8585AD;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
    background-color: transparent;
    padding: 0;
    border: none;
}

.link_active{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 24px;
    line-height: 30px;
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
    color: #F2F2F3;
}

.text{
    font-family: "JetBrainsMono", "Arial ", sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #8585AD;
    width: 320px;
    height: 48px;
    display: flex;
    align-items: center;
    margin: 80px 0 0; 
    opacity: .4;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 480px;
    width: 100%;
    height: 240px;
}

.input {
    width: 100%;
    margin-bottom: 24px;
    color: #8585AD;
}

.input > div{
    width: 100%;
}

.input > div > div{
    width: 100%;
}

.input:last-child{
    margin: 0;
}

.box_button{
    display: flex;
    align-items: center;
    max-width: 100%;
    width: 100%;
    margin-top: 24px;
}

.box_button > button:first-child{
    margin-right: 20px;
}

.button_reset{
    border: none;
	cursor: pointer;
	outline: none;
    font-family: 'Jet Brains Mono';
	font-size: 16px;
	line-height: 1.5;
	font-weight: normal;
	padding: 16px 40px;
    background: linear-gradient(63.18deg, #801ab3 0%, #4c4cff 100%);
    color: #FFF;
    border-radius: 64px;
    transition: all .3s ease-in;
}

.button_reset:hover{
    box-shadow: 0px 0px 16px 8px rgba(51, 51, 255, 0.25), 0px 0px 8px 8px rgba(51, 51, 255, 0.25);
	filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}